















































































/* @import 'assets/index_light.css'; */
* {
  padding: 0;
  margin: 0
}
html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden;
}
a {
  text-decoration: none
}
.align-center {
  text-align: center
}
li {
  list-style: none
}
.item {
  line-height: 1.6rem;
  padding: 0.1rem 0.5rem;
  color: #666;
  font-size: 0.8rem
}
.bar-item {
  line-height: 1.8rem;
  padding: 0.1rem 0.5rem;
  color: #666;
  font-size: 1rem
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity .5s ease;
}
.fade-enter,
.fade-leave-active {
  opacity: 0
}
.slide-left-enter,
.slide-right-leave-active {
  opacity: 0;
  transform: translate(30px, 0);
}
.slide-left-leave-active,
.slide-right-enter {
  opacity: 0;
  transform: translate(-30px, 0);
}
.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-line-pack: justify;
      align-content: space-between;
}
.box-col {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.box-item {
  -ms-flex-pack: center;
      justify-content: center;
}
.focus__gradient {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  background-image: -webkit-linear-gradient(top, transparent, currentColor 80%);
}
.tag-hd {
  position: relative;
  height: 2rem;
}
.h100 {
  height: 100%
}
.of-auto {
  overflow: auto
}
.sort-type {
  display: inline-block;
  padding: 0.2rem 0.4rem;
  height: 1.2rem;
  position: relative;
  font-size: 0.8rem;
  border-width: 1px;
  border-style: solid;
  border-color: #c9c9c9;
}
.tag-select {
  background-color: #31c27c;
  color: #fff;
  border-color: #31c27c;
}
.tit {
  font-size: 0.8rem;
  color: #333
}
.tit2 {
  font-size: 0.8rem;
  color: #666
}
.t_h2,
.tit_h2_w {
  font-weight: 400;
  font-size: 1.2rem;
  line-height: 1.6rem;
  color: #333
}
.tit_h2_w {
  color: #FFF
}
.tit_h4,
.tit_h4_w {
  font-weight: 300;
  font-size: 0.9rem;
  line-height: 1.2rem;
  color: #333
}
.tit_h4_w {
  color: #FFF
}
.desc {
  color: #999;
  font-size: 0.7rem
}
.top_title {
  color: #FFF;
  margin: 0.2rem 0;
  padding: 0.2rem 0;
  line-height: 1.8rem;
  font-weight: 300;
  letter-spacing: 0.2rem
}
.flex {
  display: -ms-flexbox;
  display: flex
}
.flex-flex {
  -ms-flex: 1;
      flex: 1
}
.hide {
  display: none
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  transform: rotate(0deg);
}
.rootc {
  transform: rotate(0) !important;
}



